
.listRow{
    background-color: #333333;
    margin: auto;
    margin-bottom: 10px;
    padding: 10px;
    max-width: 600px;
    display: flex;
    flex-direction: column;

    a{
        font-size: 35px;
        font-family: 'Oswald', sans-serif;
        margin-left: 10px;
        color: white;
        
        &:nth-child(2){
            font-size: 17px;
        }
    }
}


